.third-block {
	background-image: url(../img/third-block-icon-1.png), url(../img/third-block-icon-2.png), url(../img/third-block-icon-3.png), url(../img/third-block-icon-4.png), url(../img/third-block-icon-5.png), url(../img/third-block-icon-6.png), url(../img/third-block-icon-7.png);
	background-repeat: no-repeat;
	background-position: calc(50% - 390px) 135px, calc(50% - 225px) 194px, calc(50% + 320px) 220px, calc(50% + 520px) 80px, calc(50% + 600px) 460px, calc(50% - 540px) 250px, calc(50% + 165px) 130px;
	&__wrap {
		position: relative;
		padding-top: 329px;
		padding-bottom: 64px; }
	&__block {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		text-align: center;
		z-index: 1;
		p {
			max-width: 360px; } }
	&__img {
		position: absolute;
		bottom: 20px;
		left: calc(50% - 413px); } }

@media (max-width: 900px) {
	.third-block {
		background-position: calc(50% - 80px) 300px,calc(50% + 99px) 344px,calc(50% + 194px) 437px,calc(50% + 520px) 80px,calc(50% + 556px) 460px,calc(50% - 555px) 250px,calc(50% + -179px) 482px;
		&__wrap {
			padding-top: 0;
			padding-bottom: 300px; }
		&__img {
		    bottom: auto;
		    top: 150px;
    		left: calc(50% - 315px);
    		width: 630px; } } }
