.second-block {
	&__wrap {
		position: relative;
		padding-top: 211px;
		padding-bottom: 185px; }
	&__block {
		width: 510px;
		margin-left: auto; }
	&__img {
		position: absolute;
		top: -50px;
		right: 480px;
		width: 833px;
		height: 826px;
		div {
			overflow-y: hidden;
			width: 100%;
			height: 642px;
			position: absolute;
			top: 15px;
			left: 0;
			z-index: 2;
			img {
				opacity: 0; } }
		img {
			position: absolute;
			transform-origin: 50% 50%; }
		img:nth-child(1) {
			top: 0;
			left: 0;
			z-index: 1; }
		&--anim {
			div:nth-child(2) img {
				animation: anim_title_block_1 1s;
				animation-fill-mode: forwards; } } } }

@media (max-width: 900px) {
	.second-block {
		&__wrap {
			padding-top: 27px;
			padding-bottom: 350px; }
		&__block {
			width: auto;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin: auto; }
		&__img {
			top: auto;
			bottom: -205px;
			left: calc(50% - 280px);
			width: 360px;
			transform: scale(0.4);
			div {
				overflow-y: inherit;
				width: 100%;
				height: 100%; } } } }
