.nav {
	position: absolute;
	top: 80px;
	left: 0;
	width: 100%;
	background: #fff;
	z-index: 3;
	box-shadow: 0px 4px 14px #F4F4F4;
	&--scroll {
		position: fixed;
		top: 0; }
	&__btn {
		display: none;
		position: relative;
		width: 22px;
		height: 22px;
		background: transparent;
		border: none;
		span,
		span::before,
		span::after {
			display: block;
			position: absolute;
			top: calc(50% - 1px);
			left: 0;
			width: 100%;
			height: 1px;
			background: #2B2942; }
		span::before {
			content: '';
			transform: translateY(6px); }
		span::after {
			content: '';
			transform: translateY(-5px); } }
	&__btn--active {
		span {
			background: transparent; }
		span::before {
			transform: translateY(0px) rotate(-45deg); }
		span::after {
			transform: translateY(-0px) rotate(45deg); } }
	&__wrap {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 15px 0; }
	&__list {
		display: flex;
		align-item: center; }
	&__item {
		margin-right: 30px;
		&:last-child {
			margin-right: 0; } }
	&__link {
		font-weight: 500;
		font-size: 17px;
		line-height: 21px;
		letter-spacing: 0.02em;
		color: #2B2942;
		opacity: 0.8;
		transition: .2s;
		&:hover {
			opacity: 1; } } }

@media (max-width: 900px) {
	.nav {
		top: 88px;
		&--scroll {
			top: 0; }
		&__logo img {
			width: 111px; }
		&__wrap {
			position: relative;
			padding: 11px 0; }
		&__btn {
			display: block; }
		&__list {
			display: none;
			position: absolute;
			top: 100%;
			right: -15px;
			padding: 15px;
			box-shadow: 0 4px 14px #f4f4f4;
			&--active {
				display: flex;
				flex-direction: column;
				background: #fff; } } } }
