.fourth-block {
	&__wrap {
		position: relative;
		padding-top: 196px;
		padding-bottom: 175px; }
	&__block {
		width: 360px; }
	&__img {
		position: absolute;
		top: -80px;
		left: 418px;
		width: 826px;
		height: 826px;
		img {
			position: absolute;
			transform-origin: 50% 50%; }
		img:nth-child(1) {
			top: 0;
			left: 0;
			z-index: 1;
			opacity: 0; }
		img:nth-child(2) {
			top: 234px;
			left: 261px;
			opacity: 0; }
		img:nth-child(3) {
			top: 337px;
			left: 127px;
			z-index: 2;
			opacity: 0; }
		img:nth-child(4) {
			top: 455px;
			left: 255px;
			z-index: 2;
			opacity: 0; }
		&--anim {
			img:nth-child(1) {
				animation: anim_title_block_2 1s;
				animation-fill-mode: forwards; }
			img:nth-child(2) {
				animation: anim_title_block_3 1s;
				animation-delay: 0.7s;
				animation-fill-mode: forwards; }
			img:nth-child(3) {
				animation: anim_title_block_3 1s;
				animation-delay: 1.2s;
				animation-fill-mode: forwards; }
			img:nth-child(4) {
				animation: anim_title_block_3 1s;
				animation-delay: 1.7s;
				animation-fill-mode: forwards; } } } }

@media (max-width: 900px) {
	.fourth-block {
		&__wrap {
			padding-top: 140px;
			padding-bottom: 475px; }
		&__block {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			margin: auto; }
		&__img {
		    top: auto;
		    bottom: -100px;
		    left: calc(50% - 280px);
		    width: 360px;
		    transform: scale(0.4); } } }
