.eighth-block {
	&__wrap {
		padding-top: 43px;
		padding-bottom: 100px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		.btn {
			margin-right: auto;
			margin-left: auto;
			padding-right: 20px;
			padding-left: 20px;
			margin-top: 40px; } }
	&__block {
		width: 100%;
		padding: 25px 0;
		border-top: 1px solid #E6E6F0;
		&:first-child {
			border: none; } }
	&__input,
	&__text-block {
		display: none;
		margin-top: 25px; }
	&__input:checked ~ .eighth-block__text-block {
		display: block; }
	&__label {
		display: flex;
		justify-content: space-between;
		align-items: center;
		font-weight: 600;
		font-size: 26px;
		line-height: 32px;
		color: #2B2942;
		cursor: pointer;
		span {
			display: block;
			width: 24px;
			height: 18px;
			background: url(../img/arrow-down.svg) no-repeat center; } }
	&__input:checked ~ .eighth-block__label span {
		background: url(../img/arrow-up.svg) no-repeat center; }
	&__text {
		font-size: 17px;
		line-height: 150%;
		color: #2B2942;
		opacity: 0.76;
		margin-bottom: 20px;
		&:last-child {
			margin-bottom: 0; } } }

@media (max-width: 900px) {
	.eighth-block {
		&__wrap {
			padding-top: 40px;
			padding-bottom: 70px; }
		&__label {
			font-size: 20px;
			line-height: 25px;
			span {
				margin-left: 20px; } }
		&__block {
			padding: 18px 0; }
		&__text-block {
			margin-top: 18px; }
		&__text {
			font-size: 17px;
			line-height: 150%;
			margin-bottom: 10px; } } }
