.advertising {
	background: conic-gradient(from 159.87deg at 68% -745.28%, #C0C2F7 -15deg, #C0C2F7 35.43deg, #4380F0 61deg, #6C63FF 82.98deg, #6C63FF 112.5deg, #FF6584 200.63deg, #FF754A 261.03deg, #C0C2F7 345deg, #C0C2F7 395.43deg);
	backdrop-filter: blur(14px);
	&__wrap {
		display: flex; }
	&__free,
	&__pay {
		display: flex;
		align-items: center;
		font-weight: 500;
		font-size: 15px;
		line-height: 32px;
		color: #FFFFFF;
		padding: 14px 0;
		svg {
			margin-right: 10px; } }
	&__hr {
		margin: 0 35px;
		height: 60px;
		width: 1px;
		display: block;
		background: #fff;
		opacity: 0.15; } }

@media (max-width: 900px) {
		.advertising {
	    padding-top: 8px;
	    padding-bottom: 12px;
	    background: conic-gradient(from 173.83deg at 100% -23.91%, #C0C2F7 -15deg, #C0C2F7 35.43deg, #4380F0 61deg, #6C63FF 82.98deg, #6C63FF 112.5deg, #FF6584 200.63deg, #FF754A 261.03deg, #C0C2F7 345deg, #C0C2F7 395.43deg);
	    &__wrap {
	    		flex-direction: column;
	    		align-items: center; }
	    &__hr {
	    		display: none; }
	    &__free,
	    &__pay {
	    		font-size: 12px;
	    		padding: 0;
	    		text-align: center;
	    		max-width: 277px;
	    		svg {
	    				display: none; } }
    	&__pay {
	    		line-height: 150%; } } }
