.seventh-block {
	padding-bottom: 100px;
	&__wrap {
		background: url(../img/seventh-block-bg.png) no-repeat bottom;
		padding-bottom: 70px;
		padding-top: 101px;
		display: flex;
		align-items: flex-end;
		overflow: hidden; }
	&__img {
		margin-bottom: -70px;
		margin-left: 62px;
		opacity: 0;
		&--anim {
			animation: anim_title_block_1 1s;
			animation-fill-mode: forwards; } }
	&__title {
		font-weight: bold;
		font-size: 38px;
		line-height: 47px;
		color: #FFFFFF; }
	&__text {
		font-weight: 500;
		font-size: 20px;
		line-height: 150%;
		color: #FFFFFF;
		margin-top: 12px; }
	&__btn {
		color: #282C42; } }

@media (max-width: 900px) {
	.seventh-block {
		&__wrap {
			border-radius: 50px;
			background: url(../img/seventh-block-bg-2.png) no-repeat bottom;
			background-size: cover;
			flex-direction: column-reverse;
			align-items: center;
			padding-top: 60px; }
		&__img {
			margin: 0;
			margin-bottom: -150px;
			width: 400px; }
		&__block {
			display: flex;
			flex-direction: column;
			align-items: center; }
		&__title {
			text-align: center;
			font-size: 32px;
			line-height: 40px; }
		&__text {
			text-align: center;
			font-size: 18px;
			line-height: 150%; } } }
