.sixth-block {
	background: #F7F7FB;
	&__wrap {
		padding-top: 132px;
		padding-bottom: 96px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		.btn {
			margin: auto;
			margin-top: 35px; } }
	&__title {
		font-weight: bold;
		font-size: 38px;
		line-height: 47px;
		text-align: center;
		color: #2B2942; }
	&__list {
		margin-top: 70px;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap; }
	&__column {
		width: calc(33.3% - 14.5px); }
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		padding: 28px;
		padding-top: 32px;
		background: #fff;
		border-radius: 10px;
		margin-bottom: 41px;
		transform-origin: 50% 50%;
		opacity: 0;
		&::before {
			content: '';
			position: absolute;
			top: 100%;
			left: 15px;
			border: 15px solid transparent;
			border-left: 10px solid #fff;
			border-top: 10px solid #fff; } }
	&__column {
		transform-origin: 50% 50%;
		opacity: 0;
		&--anim {
			animation: anim_title_block_2 1s;
			animation-fill-mode: forwards;
			.sixth-block__item {
				animation: anim_title_block_2 1s;
				animation-fill-mode: forwards; }
			.sixth-block__item:nth-child(2) {
				animation: anim_title_block_2 1s;
				animation-delay: 0.3s;
				animation-fill-mode: forwards; } }
		&--anim:nth-child(2) {
			animation: anim_title_block_2 1s;
			animation-delay: 0.3s;
			animation-fill-mode: forwards;
			.sixth-block__item {
				animation: anim_title_block_2 1s;
				animation-delay: 0.6s;
				animation-fill-mode: forwards; }
			.sixth-block__item:nth-child(2) {
				animation: anim_title_block_2 1s;
				animation-delay: 0.9s;
				animation-fill-mode: forwards; } }
		&--anim:nth-child(3) {
			animation: anim_title_block_2 1s;
			animation-delay: 0.9s;
			animation-fill-mode: forwards;
			.sixth-block__item {
				animation: anim_title_block_2 1s;
				animation-delay: 1.2s;
				animation-fill-mode: forwards; }
			.sixth-block__item:nth-child(2) {
				animation: anim_title_block_2 1s;
				animation-delay: 1.5s;
				animation-fill-mode: forwards; } } }
	&__text {
		font-size: 17px;
		line-height: 150%;
		color: #2B2942;
		opacity: 0.76;
		margin-top: 18px; } }

@media (max-width: 900px) {
	.sixth-block {
		&__wrap {
			padding-top: 55px;
			padding-bottom: 85px; }
		&__title {
			font-size: 30px;
			line-height: 39px; }
		&__list {
			margin-top: 35px; }
		&__column {
			width: 100%; }
		&__item {
			display: none;
			margin-bottom: 30px;
			&:first-child {
				display: flex; } } } }
