.footer {
	background: #F7F7FB;
	&__wrap {
		padding: 97px 0; }
	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-bottom: 1px solid #E6E6F0;
		padding-bottom: 28px; }
	&__top .footer__bottom-text {
		display: none; }
	&__list {
		display: flex;
		align-item: center; }
	&__item {
		margin-right: 30px;
		&:last-child {
			margin-right: 0; } }
	&__link {
		font-weight: 500;
		font-size: 17px;
		line-height: 21px;
		letter-spacing: 0.02em;
		color: #2B2942;
		opacity: 0.8;
		transition: .2s;
		&:hover {
			opacity: 1; } }
	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding-top: 30px;
		&-text,
		&-text a {
			font-size: 15px;
			line-height: 18px;
			color: rgba(#2B2942, .5);
			transition: .2s; }
		&-text a:hover {
			color: #2B2942; } } }

@media (max-width: 900px) {
	.footer {
		&__wrap {
			padding: 42px 0; }
		&__top {
			flex-direction: column;
			align-items: flex-start; }
		&__list {
			flex-direction: column; }
		&__item {
			margin-top: 20px; }
		&__top .footer__bottom-text {
			display: block;
			margin-top: 10px; }
		&__bottom .footer__bottom-text:first-child {
			display: none; }
		&__bottom .footer__bottom-text:last-child {
			display: flex;
			flex-direction: column;
			span {
				display: none; } } } }
