.title-block {
	&__wrap {
		position: relative;
		padding-top: 200px;
		padding-bottom: 283px; }
	&__text {
		font-size: 20px;
		line-height: 24px;
		color: #2B2942;
		opacity: 0.5; }
	&__title {
		font-weight: bold;
		font-size: 46px;
		line-height: 57px;
		color: #2B2942;
		margin-top: 12px; }
	&__text-list {
		font-size: 17px;
		line-height: 150%;
		color: #2B2942;
		margin-top: 21px; }
	&__list {
		margin-top: 5px; }
	&__item {
		display: flex;
		align-items: center;
		font-weight: 600;
		font-size: 18px;
		line-height: 170%;
		color: #2B2942;
		svg {
			margin-right: 10px; } }
	&__btn {
		margin-top: 30px; }
	&__img {
		position: absolute;
		top: 80px;
		left: 433px;
		width: 846px;
		height: 714px;
		img {
			position: absolute;
			transform-origin: 50% 50%; }
		img:nth-child(1) {
			top: 0;
			left: 261px;
			z-index: 1;
			opacity: 0; }
		img:nth-child(2) {
			top: 248px;
			left: 97px;
			z-index: 2;
			opacity: 0; }
		img:nth-child(3) {
			top: 95px;
			left: 138px;
			opacity: 0; }
		img:nth-child(4) {
			top: 237px;
			left: 680px;
			opacity: 0; }
		img:nth-child(5) {
			top: 561px;
			left: 5px; }
		img:nth-child(6) {
			top: 0;
			left: 647px; }
		img:nth-child(7) {
			top: 470px;
			left: 150px; }
		img:nth-child(8) {
			top: 115px;
			left: 740px; }
		&--anim {
			img:nth-child(1) {
				animation: anim_title_block_1 1s;
				animation-fill-mode: forwards; }
			img:nth-child(2) {
				animation: anim_title_block_1 1s;
				animation-delay: .7s;
				animation-fill-mode: forwards; }
			img:nth-child(3) {
				animation: anim_title_block_2 1.5s;
				animation-delay: 1.2s;
				animation-fill-mode: forwards; }
			img:nth-child(4) {
				animation: anim_title_block_2 1.5s;
				animation-delay: 1.7s;
				animation-fill-mode: forwards; } } } }

@media (max-width: 900px) {
	.title-block {
		&__wrap {
			padding-top: 40px;
			padding-bottom: 325px; }
		&__text {
			font-size: 16px;
			line-height: 19px; }
		&__title {
			font-size: 30px;
			line-height: 37px; }
		&__text-list {
			font-size: 16px;
			line-height: 150%; }
		&__item {
			font-size: 16px;
			line-height: 170%;
			svg {
				margin-top: 4px;
				margin-bottom: auto; } }
		&__btn {
			display: none; }
		&__img {
		    top: auto;
		    bottom: -205px;
		    left: calc(50% - 280px);
		    width: 360px;
		    transform: scale(0.4); } } }
