.fifth-block {
	&__wrap {
		padding-top: 90px;
		padding-bottom: 46px; }
	&__text {
		font-size: 20px;
		line-height: 24px;
		color: #2B2942;
		opacity: 0.5;
		text-align: center; }
	&__title {
		font-weight: bold;
		font-size: 38px;
		line-height: 47px;
		color: #2B2942;
		margin-top: 20px;
		text-align: center; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-top: 60px; }
	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: calc(50% - 10px);
		padding: 100px 20px;
		text-align: center;
		background: #F9F8FF;
		margin-bottom: 20px;
		transform-origin: 50% 50%;
		opacity: 0;
		&:nth-child(2) {
			background: #FFF8F8; }
		&:nth-child(3) {
			background: #F8FFFA; }
		&:nth-child(4) {
			background: #FDF8FF; }
		&-title {
			font-weight: 600;
			font-size: 28px;
			line-height: 130%;
			color: #2B2942;
			max-width: 349px;
			margin-top: 24px; }
		&-text {
			font-size: 18px;
			line-height: 150%;
			color: #2B2942;
			opacity: 0.76;
			max-width: 349px;
			margin-top: 14px; }
		&--anim {
			animation: anim_title_block_2 1s;
			animation-fill-mode: forwards; }
		&--anim:nth-child(2) {
			animation: anim_title_block_2 1s;
			animation-delay: 0.5s;
			animation-fill-mode: forwards; }
		&--anim:nth-child(3) {
			animation: anim_title_block_2 1s;
			animation-delay: 1s;
			animation-fill-mode: forwards; }
		&--anim:nth-child(4) {
			animation: anim_title_block_2 1s;
			animation-delay: 1.5s;
			animation-fill-mode: forwards; } } }


@media (max-width: 900px) {
	.fifth-block {
		&__wrap {
			padding-top: 0; }
		&__title {
			font-size: 30px;
			line-height: 1.3;
			margin-top: 10px; }
		&__list {
			margin-top: 20px; }
		&__item {
			width: 100%;
			padding-top: 42px;
			padding-bottom: 42px;
			margin-bottom: 15px;
			&-title {
				font-size: 22px;
				line-height: 130%;
				margin-top: 10px; }
			&-text {
				font-size: 17px;
				line-height: 150%;
				margin-top: 5px; } } } }
