body {
	min-width: 320px;
	margin: 0;
	padding: 0;
	font-family: 'Gilroy', sans-serif;
	line-height: 1.3;
	letter-spacing: 0.02em;
	background: #fff; }

*, *:before, *:after {
	box-sizing: border-box;
	outline: none;
	text-decoration: none;
	margin: 0;
	padding: 0; }

ul,ol {
	list-style: none; }

iframe {
	width: 100%; }
main {
	overflow: hidden; }
.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0px;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden; }

.text-title {
	font-weight: bold;
	font-size: 34px;
	line-height: 130%;
	color: #2B2942; }
.text {
	font-size: 18px;
	line-height: 160%;
	color: #2B2942;
	opacity: 0.8;
	margin-top: 25px; }
.btn {
	display: inline-flex;
	justify-content: center;
	align-items: center;
	font-weight: bold;
	font-size: 16px;
	line-height: 32px;
	text-align: center;
	color: #6C63FF;
	background: #FFFFFF;
	border: 1px solid #D7DBEB;
	border-radius: 28px;
	padding: 7px 17px;
	min-width: 180px;
	margin-top: 25px;
	transition: .2s;
	&:hover {
		color: #fff;
		background: #6C63FF;
		border-color: #6C63FF; } }

@keyframes anim_title_block_1 {
	0% {
		opacity: 0;
		transform: translate(0%,100%); }
	100% {
		opacity: 1;
		transform: translate(0%,0%); } }

@keyframes anim_title_block_2 {
	0% {
		opacity: 0; }
	100% {
		opacity: 1; } }

@keyframes anim_title_block_3 {
	0% {
		opacity: 0;
		transform: translate(100%,0%); }
	100% {
		opacity: 1;
		transform: translate(0%,0%); } }

@media (max-width: 900px) {
	.text-title {
		font-size: 28px;
		line-height: 130%;
		text-align: center; }
	.text {
		font-size: 17px;
		margin-top: 18px;
		text-align: center; } }
